<template>
  <div class="loginWrapper" id="loginBackground">
    <div class="formWrapper">
      <h1 class="loginTitle">{{loginTitle}}</h1>
      <p class="loginSystem">{{loginSystem}}</p>
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
        hide-required-asterisk
      >
        <el-form-item prop="userName">
          <el-input
            prefix-icon="el-icon-mobile-phone"
            v-model="ruleForm.userName"
            placeholder=""
          ></el-input>
        </el-form-item>
        <el-form-item prop="password">
          <el-input
            prefix-icon="el-icon-lock"
            v-model="ruleForm.password"
            placeholder=""
            show-password
          ></el-input>
        </el-form-item>
        <el-form-item></el-form-item>
        <el-form-item class="forgetPassword"></el-form-item>
        <el-form-item class="loginButtonWrapper">
          <el-button class="loginButton" type="primary" @click="submitForm('ruleForm')"></el-button>
        </el-form-item>
        <el-form-item style="text-align:left;">
          
          <a href="/api/user/login/qq">
            <img :src="qqIcon" />
          </a>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import CanvasNest from "canvas-nest.js";

const config = {
  // 
  color: "64, 158, 255", // 
  pointColor: "64, 158, 255", // 
  opacity: 0.5, // 
  count: 99, // 
  zIndex: -1 // 
};

import { request } from "http";
export default {
  name: "Login",
  components: {},
  data() {
    return {
      loginTitle: "",
      loginSystem: "SCP",
      ruleForm: {
        userName: "",
        password: ""
      },
      rules: {
        userName: [
          { required: true, message: "", trigger: "blur" }
        ],
        password: [
          { required: true, message: "", trigger: "blur" },
          { min: 5, max: 20, message: " 5  20 ", trigger: "blur" }
        ]
      },
      loginState: false,
      qqIcon: require("@/assets/images/login/qqIcon.png")
    };
  },
  computed: {
    url() {
      let _url = this.$route.query.Rurl; //next
      if (_url) {
        //
        return _url;
      } else {
        //
        return "/";
      }
    }
  },
  methods: {
    async submitForm(formName) {
      await this.$refs[formName].validate(valid => {
        if (valid) {
          //
          var info = {
            username: this.ruleForm.userName,
            password: this.ruleForm.password
          };
          var _this = this;
          this.$http.login(info).then(function(res) {
            if (res.status === 200) {
              if (res.data.success == true) {
                _this.$store.commit("changeLogin", true);
                _this.$router.replace({ path: _this.url });
              } else {
                _this.$message.error("");
              }
            } else {
              console.log(res);
            }
          });
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    }
  },
  created() {
    this.$emit("footer", false);
    if (this.$store.state.isLogin == true) {
      // 
      let username = this.$store.state.username;
      this.$message({
        message: username + " ",
        center: true,
        type: "success"
      });
      this.$router.replace({ path: "/" });
    }
    this.$nextTick(() => {
      let element = document.getElementById("loginBackground");
      const cn = new CanvasNest(element, config);
    });
  }
};
</script>
<style lang="stylus" scoped>
.loginWrapper {
  height: 500px !important;
  min-height: 500px !important;
  padding-top: 100px;

  .formWrapper {
    width: 375px;
    margin: 0 auto;
    text-align: center;

    .loginTitle {
      margin-bottom: 10px;
      font-weight: 300;
      font-size: 30px;
      color: #000;
    }

    .loginSystem {
      font-weight: 300;
      color: #999;
    }

    .demo-ruleForm {
      width: 100%;
      margin-top: 20px;

      >>> .el-form-item__content {
        margin-left: 0 !important;
      }

      &>>> .el-input__inner {
        font-size: 16px;
      }

      .forgetPassword {
        text-align: right;
        margin: -22px 0 0 0;
      }

      .loginButtonWrapper {
        .loginButton {
          width: 100%;
        }

        &>>> .el-button {
          padding: 10px 90px;
          font-size: 16px;
        }
      }
    }

    .tip {
      width: 70%;
      margin-left: 86px;
    }
  }
}
</style>
